/**
 * ÂÖ®Â±ÄÂèòÈáè
 */
// ‰∏ã‰∏Ä‰∏™Â∑•‰ΩúÂçïÂÖÉ
let nextUnitOfWork = null;
// react ÂèåÁºìÂ≠ò Ë∑üË∏™ÂΩìÂâçÊ∏≤ÊüìÁöÑËäÇÁÇπ
// hooks Êï∞ÊçÆÁªìÊûÑ Ê†à
let wipFiber = null;
// ÂΩìÂâçÁöÑÂ∑•‰ΩúÂçïÂÖÉ
let wipRoot = null;
// ÂΩìÂâçÊ∏≤ÊüìÊ†ë ÊúÄÂêéÂáÜÂ§áÊèê‰∫§ÁöÑÊ∏≤ÊüìÊ†ë
let currentRoot = null;
// ÂáÜÂ§áÂà†Èô§ÁöÑËäÇÁÇπÊ†ë
let deletions = null;

function createDom(fiber) {
  const dom = fiber.type === 'TEXT_ELEMENT'
    ? document.createTextNode('')
    : document.createElement(fiber.type)
  updateDom(dom, {}, fiber.props)
  return dom
}

const isProperty = key => key !== 'children';

//Âà§Êñ≠ÊòØÂê¶ÊòØÊñ∞Â±ûÊÄß
const isNew = (prev, next) => key => prev[key] !== next[key];
// ÊòØÂê¶ÊòØÊóßÂ±ûÊÄß
const isGone = (prev, next) => key => !(key in next)

const isEvent = key => key.startsWith('on')

function updateDom(dom, prevProps, nextProps) {
  // Âà†Èô§ÊóßÂ±ûÊÄß
  Object.keys(prevProps)
    .filter(isProperty)
    .filter(isGone(prevProps, nextProps))
    .forEach(name => dom[name] = '')
  // Êõ¥Êñ∞Êñ∞Â±ûÊÄß
  Object.keys(nextProps)
    .filter(isProperty)
    .filter(isNew(prevProps, nextProps))
    .forEach(name => dom[name] = nextProps[name])
  // Âà†Èô§ÊóßÁöÑÊàñËÄÖÊúâÂèòÂåñÁöÑ‰∫ã‰ª∂
  Object.keys(prevProps)
    .filter(isEvent)
    .filter(key => !(key in nextProps) || isNew(prevProps, nextProps)(key))
    .forEach(name => {
      const eventType = name.toLowerCase().substring(2)
      dom.removeEventListener(eventType, prevProps[name])
    })
  // Ê≥®ÂÜåÊñ∞‰∫ã‰ª∂
  Object.keys(nextProps)
    .filter(isEvent)
    .filter(isNew(prevProps, nextProps))
    .forEach(name => {
      const eventType = name.toLowerCase().substring(2)
      dom.addEventListener(eventType, nextProps[name])
    })
}

/**
 * Âà†Èô§Dom
 * Êó†ËÆ∫Âà†Èô§ËøòÊòØÊåÇËΩΩÈÉΩÈúÄË¶ÅÊâæÂà∞Dom
 * @param fiber
 * @param domParent
 */
function commitDeletion(fiber, domParent) {
  if (fiber.dom) {
    domParent.removeChild(fiber.dom);
  } else {
    commitDeletion(fiber.child, domParent);
  }
}

function commitRoot() {
  deletions.forEach(commitWork)
  commitWork(wipRoot.child)
  // ÂèåÁºìÂ≠òÂØπÂ∫îÁöÑÊ∏≤ÊüìÊ†ë
  currentRoot = wipRoot
  wipRoot = null
}

/**
 * Êèê‰∫§Â∑•‰ΩúÂçïÂÖÉ
 * @param fiber
 */
function commitWork(fiber) {
  if (!fiber) {
    return
  }
  // Êõ¥Êñ∞ dom
  let domParentFiber = fiber.parent
  // Â¶ÇÊûúfiber.parent Ê≤°Êúâ domËäÇÁÇπÔºå‰∏ÄÁõ¥Âêë‰∏äparentÊü•ÊâæÔºåÁõ¥Âà∞ÊâæÂà∞ dom ËäÇÁÇπ
  while (!domParentFiber.dom) {
    domParentFiber = domParentFiber.parent
  }
  const domParent = domParentFiber.dom
  if (fiber.effectTag === 'PLACEMENT' && fiber.dom !== null) {
    domParent.appendChild(fiber.dom)
  } else if (fiber.effectTag === 'UPDATE' && fiber.dom !== null) {
    updateDom(fiber.dom, fiber.alternate.props, fiber.props)
  } else if (fiber.effectTag === 'DELETION') {
    commitDeletion(fiber, domParent)
  }
  commitWork(fiber.child)
  commitWork(fiber.sibling)
}

/**
 * Ê∏≤Êüì render
 * @param element
 * @param container
 */
function render(element, container) {
  // ÈÄíÂΩíÔºö render diff koa-compose async/await
  // 1„ÄÅËπ¶Â∫äÂáΩÊï∞ ‰∏ÄÂÆö‰∏çÁàÜÊ†à EC ÊãâÂπ≥ while
  // 2„ÄÅÂ∞æË∞ÉÁî®‰ºòÂåñ
  // 3„ÄÅwhile Âæ™ÁéØ‰ºòÂåñ V8 10000 for O(n)
  // 4„ÄÅÈÅçÂéÜÊ†ë while Ê∑±Â∫¶‰ºòÂÖà ÂπøÂ∫¶‰ºòÂÖà
  // const dom = element.type == 'TEXT_ELEMENT'
  //   ? document.createTextNode('')
  //   : document.createElement(element.type);
  // const isProperty = key => key !== 'children';
  // Object.keys(element.props)
  //   .filter(isProperty)
  //   .forEach((name) => {
  //     dom[name] = element.props[name];
  //   });
  // element.props.children.forEach(child => render(child, dom));
  // container.appendChild(dom);


  // Èò∂ÊÆµ‰∫å
  // Á¨¨‰∏Ä‰∏™Â∑•‰ΩúÂçïÂÖÉ
  wipRoot = {
    dom: container,
    props: {
      children: [element],
    },
    // Âà§Êñ≠ÈìæË°®‰∏≠Ê≠£Âú®Â∑•‰ΩúÁöÑÔºåÂàáÊç¢
    alternate: currentRoot,
  }
  deletions = []
  // ‰∏ã‰∏Ä‰∏™fiber ËäÇÁÇπÔºàÂ∑•‰ΩúÂçïÂÖÉÔºâÁöÑË∑üËäÇÁÇπ
  nextUnitOfWork = wipRoot
}

function workLoop(deadline) {
  // 1„ÄÅËé∑Âèñ‰ªªÂä°
  // 2„ÄÅÊâßË°å‰ªªÂä°
  // 3„ÄÅÂ∞Ü‰ªªÂä°ÊîæÂÖ•ÈòüÂàó
  // 4„ÄÅÂæ™ÁéØ
  // 5„ÄÅÂæ™ÁéØÁªìÊùü
  // 6„ÄÅÊâßË°åÂÆåÊàê
  // Âà§Êñ≠ÊòØÂê¶Â∫îËØ•ÂÅúÊ≠¢Âæ™ÁéØ
  let shouldYield = false;
  while (nextUnitOfWork && !shouldYield) {
    nextUnitOfWork = performUnitOfWork(nextUnitOfWork)
    // Â¶ÇÊûúÂΩìÂâç‰ªªÂä°ÁöÑÊà™Ê≠¢Êó∂Èó¥Âà∞‰∫Ü ÂÅúÊ≠¢Â∑•‰ΩúÂæ™ÁéØ ÊâßË°åÊõ¥È´ò‰ºòÂÖàÁ∫ßÁöÑ‰ªªÂä°
    console.log("deadlineüöó", deadline.timeRemaining())
    console.log("üå≤", nextUnitOfWork)
    shouldYield = deadline.timeRemaining() < 1;
  }

  if (!nextUnitOfWork && wipRoot) {
    // Êèê‰∫§Èò∂ÊÆµ
    commitRoot();
  }

  requestIdleCallback(workLoop)
}
// ÂëäÁü•ÊµèËßàÂô®Ë∞ÉÂ∫¶
requestIdleCallback(workLoop)

/**
 * Êõ¥Êñ∞ÂáΩÊï∞ÁªÑ‰ª∂
 * @param fiber
 */
function updateFunctionComponent(fiber) {
  wipFiber = fiber
  const children = [fiber.type(fiber.props)]
  wipFiber.hooks = []
  reconcileChildren(fiber, children)
}

/**
 * Êõ¥Êñ∞ÊôÆÈÄöËäÇÁÇπ
 * @param fiber
 */
function updateHostComponent(fiber) {
  // Á¨¨‰∏ÄÂõû Ê∏≤ÊüìÂà∞È°µÈù¢‰∏≠Âéª
  if (!fiber.dom) {
    fiber.dom = createDom(fiber)
  }
  // dom tree
  const children = fiber.props.children
  // Ë∞ÉÂíå dom diff tree
  reconcileChildren(fiber, children)
}

/**
 * ÊâøËΩΩ‰∏ã‰∏Ä‰∏™Â∑•‰ΩúÂçïÂÖÉ
 */
function performUnitOfWork(fiber) {
  // Âà§Êñ≠ÂΩìÂâçËäÇÁÇπÊòØ‰∏çÊòØÂáΩÊï∞ÁªÑ‰ª∂
  const isFunctionComponent = fiber && fiber.type && fiber.type instanceof  Function

  if (isFunctionComponent) {
    updateFunctionComponent(fiber)
  } else {
    updateHostComponent(fiber)
  }

  if (fiber.child) {
    return fiber.child
  }
  let nextFiber = fiber
  while (nextFiber) {
    if (nextFiber.sibling) {
      return nextFiber.sibling
    }
    nextFiber = nextFiber.parent
  }
}

/**
 * dom diff
 * ÊûÑÂª∫ÊàêÂèåÂêëÈìæË°®
 * @param fiber
 * @param elements
 */
function reconcileChildren(fiber, elements) {
  let index = 0
  // ÂèòÂåñ‰πãÂâçÁöÑËäÇÁÇπ
  let oldFiber = fiber.alternate && fiber.alternate.child
  let prevSibling = null
  // ÈÅçÂéÜ children
  while (index < elements.length || oldFiber != null) {
    // jsx --> fiber
    const element = elements[index]
    let newFiber = null;
    const sameType = oldFiber && element && element.type === oldFiber.type
    // Â¶ÇÊûúÊòØÂêåÊ†∑ÁöÑËäÇÁÇπÁ±ªÂûã
    if (sameType) {
      newFiber = {
        type: oldFiber.type,
        props: element.props,
        dom: oldFiber.dom,
        parent: fiber,
        alternate: oldFiber,
        effectTag: 'UPDATE'
      }
    }
    // Â¶ÇÊûúËäÇÁÇπÁ±ªÂûã‰∏ç‰∏ÄËá¥ Êñ∞Â¢ûËäÇÁÇπ
    if (!sameType && element) {
      newFiber = {
        type: element.type,
        props: element.props,
        dom: null,
        parent: fiber,
        alternate: null,
        effectTag: 'PLACEMENT'
      }
    }
    // Â¶ÇÊûúËäÇÁÇπÁ±ªÂûã‰∏ç‰∏ÄËá¥ Êñ∞ËäÇÁÇπ‰∏çÂ≠òÂú®
    if (!sameType && oldFiber) {
      oldFiber.effectTag = 'DELETION'
      deletions.push(oldFiber)
    }
    // Â¶ÇÊûúÂ≠òÂú® old ËäÇÁÇπ
    if (oldFiber) {
      oldFiber = oldFiber.sibling
    }
    if (index === 0) {
      fiber.child = newFiber
    } else if (element) {
      // TODO: fiber sibling ÊÄé‰πàÂàõÂª∫Ê≥®ÂÖ•ÁöÑ
      prevSibling.sibling = newFiber
    }
    // js Ê®°ÊãüÊåáÈíàÁßªÂä®?
    prevSibling = newFiber
    index++
  }
}

/**
 * hook: useState
 * initial ÂàùÂßãÂèÇÊï∞
 * @param initial
 * @returns {(*|setState)[]}
 */
function useState(initial) {
  // ÊòØÂê¶ÊúâÊóßÁöÑÈí©Â≠êÔºå ÊóßÈí©Â≠êÂ≠òÂÇ®‰∫Ü‰∏ä‰∏ÄÊ¨°Êõ¥Êñ∞ÁöÑ hook
  const oldHook = wipFiber.alternate && wipFiber.alternate.hook
  // ÂàùÂßãÂåñÈí©Â≠ê Èí©Â≠êÁöÑÁä∂ÊÄÅÊòØÊóßÁöÑÈí©Â≠êÁöÑÁä∂ÊÄÅÊàñËÄÖÂàùÂßãÁä∂ÊÄÅ
  const hook = {
    state: oldHook ? oldHook.state : initial,
    queue: []
  }
  // ‰ªéÊóßÁöÑÈí©Â≠êÈòüÂàó‰∏≠Ëé∑ÂèñÊâÄÊúâÁöÑÂä®‰Ωú ÁÑ∂ÂêéÂ∞Ü‰ªñ‰ª¨‰∏Ä‰∏ÄÂ∫îÁî®Âà∞Êñ∞ÁöÑÈí©Â≠êÁöÑÁä∂ÊÄÅ‰∏ä
  const actions = oldHook ? oldHook.queue : []
  actions.forEach(action => {
    hook.state = action(hook.state)
  })
  // ËÆæÁΩÆÈí©Â≠êÁä∂ÊÄÅ
  const setState = (action) => {
    hook.queue.push(action)
    // Êõ¥Êñ∞Ê∏≤Êüì
    wipRoot = {
      dom: currentRoot.dom,
      props: currentRoot.props,
      alternate: currentRoot
    }
    nextUnitOfWork = wipRoot
    deletions = []
    // hook.queue.push(state => state)
    // scheduleWork(wipFiber)
  }
  // ÊääÈí©Â≠êÊ∑ªÂä†Ëá≥Â∑•‰ΩúÂçïÂÖÉ
  wipFiber.hook = hook

  // ËøîÂõûÈí©Â≠êÁöÑÁä∂ÊÄÅÂíåËÆæÁΩÆÈí©Â≠êÁöÑÂáΩÊï∞
  return [hook.state, setState]
}

export { render, useState }
